/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }
  blockquote:before, blockquote:after, q:before, q:after {
    content: '';
    content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

.wrapper-block {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  width: 964px; }
  .wrapper-block:after {
    content: " ";
    display: block;
    clear: both; }

.row {
  margin-left: -5px;
  margin-right: -5px; }
  .row::after {
    clear: both;
    content: "";
    display: table; }

h1 {
  color: #1092dd;
  font-size: 48px;
  line-height: 36px;
  font-weight: 300;
  margin-bottom: 40px; }

h2 {
  color: #1b1a1b;
  font-size: 30px;
  line-height: 36px;
  font-weight: 300;
  margin-bottom: 30px; }

h3 {
  color: #1b1a1b;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  margin-bottom: 30px; }

h4 {
  color: #1b1a1b;
  font-size: 20px;
  line-height: 24px;
  font-weight: 400;
  margin-bottom: 20px; }

.wrapper-block {
  width: 964px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

.wrapper {
  position: relative; }

body {
  font-family: 'Roboto', Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  color: #1b1a1b;
  background: url(../img/main-bg.jpg); }

a {
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out; }

.wrapper {
  min-height: 100%;
  padding-bottom: 40px;
  margin-bottom: -188px; }

.wrapper:after {
  content: "";
  display: block; }

.footer-site, .wrapper:after {
  height: 188px; }

@media (max-width: 991px) {
  .wrapper-block {
    width: 620px; } }

@media (max-width: 767px) {
  .wrapper-block {
    width: 100%;
    min-width: 320px; } }
